import React, {useState} from "react";
import Header from "./header";
import Footer from "./footer";
import Note from "./note";
import notes from "./notes.js";
import CreateArea from "./newNote";




function App(){
    
    const [notesList, setNote] = useState([])

    function deleteNote(id){
        //this recieves the props.ID from onChecked function in note.jsx
        console.log(id);
        setNote((prevItems) => {
            return(prevItems.filter((item, index) => index!==id))
        });
    }

    function addNote(newNote){
        // simply adds new note to end of the list. .push isn't an option 
        // while using state hooks
        setNote(prevValue =>{
            return[...prevValue, newNote];
        })
    }
    //console.log(Header());
    return(
        <div>
            <Header />
            <CreateArea 
                onSubmit={addNote}
            />

            {/* Creates a note for each item in notes.js which is a list */}
            {notesList.map((notesList, index) => ( 
                <Note
                //key required for map function id required to delete note
                    key={index}
                    id={index} 
                    title={notesList.title}
                    body={notesList.body} 
                    onChecked={deleteNote} 
                />
            ))}

            <form action="../../post" method="post" 
              className="form">
            <button type="submit">Connected?</button>
            </form>
            <Footer />
        </div>
        );
}   

export default App;









